$templateCache.put("app/main/main.html","<div id=masthead class=\"jumbotron vertical-center text-center\"><div class=container><h1 class=\"wow fadeInDown\">NII MANTE</h1><p class=\"lead wow fadeInDown\">Full Stack Software Engineer</p><h4 class=\"socials wow fadeInUp\"><span ng-repeat=\"social in main.socials\"><a href={{social.link}} target=_blank><i id={{social.name}} class={{social.icon}}></i></a></span></h4></div></div><div class=container-fluid><div id=about class=\"row text-center\"><br><br><h3>ABOUT NII</h3><div class=\"col-sm-2 col-xs-1 col-md-3 col-lg-3\"></div><div class=\"col-sm-8 col-xs-10 col-md-6 col-lg-6 text-justify\"><br><p>I\'m Nii. I\'m a PhD candidate at the University of Southern California. I\'ll be graduating from there in 2016 with an MS in Computer Science and PhD in Biomedical Engineering. I\'ve been researching and building computer vision systems to help the blind. I graduated from the University of Maryland in 2010 with a BS in Bioengineering.</p><br><br><br></div><div class=\"col-sm-2 col-xs-1 col-md-3 col-lg-3\"></div><br><br><br><div class=\"col-xs-12 col-sm-6 col-md-6 col-lg-6 text-center wow slideInUp\" ng-repeat=\"about in main.abouts\"><img ng-src=assets/images/{{about.img}} alt=\"{{ about.alt }}\"><br><br><p class=lead>{{ about.title }}</p><p class=about-col>{{ about.text }}</p><br><br><br></div></div><div id=project class=\"row text-center\"><br><br><h3>PROJECTS</h3><br><div class=\"col-xs-12 col-sm-6 col-md-6 col-lg-6\" ng-repeat=\"project in main.projects\"><div class=thumbnail><div class=\"col-xs-3 col-sm-3 col-md-3 col-lg-3\"><br><a ng-href=#/projects/{{project.id}}><img style=\"padding: 0\" class=pull-left ng-src=\"assets/images/{{ project.logo }}\" alt=\"{{ project.title }}\"></a></div><div class=\"col-xs-9 col-sm-9 col-md-9 col-lg-9 text-left\"><!-- <div class=\"caption text-left\"> --> <a ng-href=#/projects/{{project.id}}><h4 class=lead>{{ project.title }}</h4></a><p>{{ project.description }} <a ng-href=#/projects/{{project.id}}>...more</a></p><span></span><p><i class=\"fa fa-tags\" aria-hidden=true></i> <span ng-bind-html=project.tagline></span></p><!-- </div> --></div></div></div></div><div id=contact class=row><br><br><br><div class=\"col-xs-12 col-sm-6 col-md-6 col-lg-6 text-center\"><h3 style=\"color: #fff\" class=text-center>CONTACT NII</h3><p class=lead>Feel free to contact me about business, or coding</p><br></div><div class=\"col-xs-12 col-sm-6 col-md-6 col-lg-6 text-center\"><p class=lead><i class=\"fa fa-user\" aria-hidden=true></i> NII MANTE</p><p class=lead><i class=\"fa fa-location-arrow\" aria-hidden=true></i> LOS ANGELES, CA</p><p class=lead><i class=\"fa fa-envelope\" aria-hidden=true></i> <a href=mailto:nii@niimante.com>NII@NIIMANTE.COM</a></p><br><br><br></div></div><div id=social class=row><!--  social icons --><div class=\"socials col-lg-4 col-md-4 col-sm-4 col-xs-4 text-center\" ng-repeat=\"social in main.socials\"><br><a href={{social.link}} target=_blank><i class=\"{{social.icon}} social-icon\"></i></a><br><br></div></div></div>");
$templateCache.put("app/projects/projects.html","<div ng-include=projectsController.project.template></div>");
