$templateCache.put("app/components/navbar/navbar.html","<nav class=\"navbar navbar-default navbar-fixed-top wow slideInDown\"><div class=container><div class=navbar-header><a class=navbar-brand href=/ >NM </a><button type=button class=\"navbar-toggle collapsed\" data-toggle=collapse data-target=#navbar aria-expanded=false aria-controls=navbar><span class=sr-only>Toggle navigation</span> <span class=icon-bar></span> <span class=icon-bar></span> <span class=icon-bar></span></button></div><div class=\"collapse navbar-collapse\" id=navbar><ul class=\"nav navbar-nav navbar-right\"><!-- <li class=\"dropdown\">\n          <a href=\"#\" class=\"dropdown-toggle\" data-toggle=\"dropdown\" role=\"button\" aria-haspopup=\"true\" aria-expanded=\"false\">\n            Home <span class=\"caret\"></span>\n          </a>\n          <ul class=\"dropdown-menu\">\n            <li><a ng-href=\"#about\" target=\"_self\">About</a></li>\n            <li><a ng-href=\"#project\" target=\"_self\">Projects</a></li>\n            <li><a ng-href=\"#contact\" target=\"_self\">Contact</a></li>\n          </ul>\n        </li> --><li><a ng-href=/ >Home</a></li><li><a ng-href=#about target=_self>About</a></li><li><a ng-href=#project target=_self>Projects</a></li><li><a ng-href=#contact target=_self>Contact</a></li><li><a ng-href=#/blog>Blog</a></li><li><a ng-href=#/resume>Resume <i class=\"fa fa-download\" aria-hidden=true></i></a></li></ul></div></div></nav>");
$templateCache.put("app/model/projects/dedup.html","<div class=\"jumbotron text-center dedup-jumbotron-bg\"></div><div class=\"container-fluid wow fadeInUp\"><div class=\"row text-center project-text-col\"><h1 class=\"wow fadeInDown\">{{projectsController.project.title}}</h1><h3 class=\"lead wow fadeInUp\">{{projectsController.project.description}}</h3></div><div id=images class=\"row proxee-text-col\"><div class=\"col-xs-12 col-sm-12 col-md-2 col-lg-2\"></div><div class=\"col-md-8 col-lg-8 project-content\"><h1 class=\"lead text-center\">OVERVIEW</h1><p>Dedup is a python program I created as apart of my Information Retrieval class. The premise relates to search engines and web crawlers. During the class, 3 group members and I built a web crawler to grab images and webpages from the internet. Web crawlers are tools/programs that automatically grab/index content from the internet.</p><p>Essentially when web crawlers are indexing/crawling/downloading web pages on the internet there are times when the same data may be crawled (aka \"looked at\") repeatedly. Because of this you could end up downloading the same webpages, images or content; this can lead to unnecessary waste of memory or even worse, your crawler getting stuck (\'spider traps\'). Thus, deduplication is one technique used to get around these problems.</p><p>Feel free to check out the project on <a ng-href={{projectsController.project.url}} target=_blank>Github</a></p><h1 class=lead>MY TASKS</h1><p>To accomplish deduplication I:<ul><li><b>PROGRAM DESIGN:</b> Came up with a pipeline for a command line application</li><li><b>ALGORITHM DESIGN</b> Came up with a way to \'featurize\' images to efficiently compare them</li></ul></p><p>The entire program was written in <b>Python</b>. I chose Python because it\'s got an awesome abstraction for multiprocess programming that makes it quick write highly parallel programs.</p></div><div class=\"col-xs-12 col-sm-12 col-md-2 col-lg-2\"></div></div><div class=\"row text-center proxee-text-col\"><h1 class=lead>PIPELINE</h1><div class=\"col-xs-12 col-sm-12 col-md-12 col-lg-12\"><img class=dedup-limit src=assets/images/dedup-architecture.tiff alt=\"\"><h6><b>Figure 1:</b> The deduplication pipeline.</h6><br></div></div><br><div id=roommate class=\"row proxee-text-col\"><br><div class=\"col-md-2 col-lg-2\"></div><div class=\"col-xs-12 col-sm-12 col-md-8 col-lg-8 text-justify\"><p><b>Figure 1</b> shows the deduplication pipeline. This architecture allowed me to write a program that can deduplicate <b>~70000</b> images in about <b>10 seconds</b>.</p><br><h4 class=text-center><b>Techie Section:</b> What\'s happening behind the scenes?</h4><p><ul><li>Near De-duplication</li><li>Exact De-duplication</li><li>\"MapReduce\"</li></ul>It was extremely necessary to write a program that could deduplicate efficiently. No one wants to wait forever for a task such as this. To speed up the process, the python program:<ul><li><b>Split</b> Splits the data into M smaller segments</li><li><b>Workers</b> Creates M worker processes</li><li><b>Deduplicate</b> Tells each worker process to deduplicate it\'s smaller data chunk</li><li><b>Merge</b> Then merge the deduplicated images from the smaller sets</li></ul></p><p>The deduplication has two methodologies. One is \'exact\' deduplication, and the other is \'near\' deduplication. An exact duplicate set of images would have all the same exact pixels in the same exact positions. A nearly duplicate set of images could be the same images with slightly differing brightness, or slightly different pixel values.</p><h4>Exactly!</h4><p>Exact deduplication can be done fairly quickly, by simply \'hashing\' the image bytes and storing the hash/value pairs in a lookup table/dictionary. This ends up being extremely efficient, and you can iterate and deduplicate the data in one pass. Then add that to the fact that it\'s working on multiple cores/processes and you get a blazingly fast program.</p><h4>The end is Nigh... Near?</h4><p>Near deduplication is slightly different. In this case, you can\'t just simply \'hash\' the image. If the images are different in ANY way, and you hash them, the resultant hashes will be unbelievably different. That\'s a desired result of hashing, but it doesn\'t suit the problem we\'re trying to solve.</p><p>It would be nice if we could get similar hashes when the items were only slightly different. Turns out that some awesome people at Google came up with what\'s called <code>Simhash</code>. Essentially, it gives us the ability to reduce a huge piece of data (an image) to a small \'fingerprint\'! In addition to reducing the data, it also lets two pieces of data that are somewhat \'similar\' to have similar hashes (aka sim... hashes...). To generate the finger print I used features such as the image height, width, portions of the image bytes, etc.</p><p>I applied a similar high level logic to solving the near de-duplication problem. Instead of simply seeing if a hash value exists within the lookup table, I check to see if a \'nearly\' similar hash value exists within the lookup table. To speed this up even more, I use an index which sits on top of the data.</p><br></div><div class=\"col-md-2 col-lg-2\"></div></div></div>");
$templateCache.put("app/model/projects/liltron.html","<div class=\"jumbotron liltron-jumbotron text-center liltron-jumbotron-bg wow fadeInDown\"></div><div class=\"container-fluid wow fadeInUp\"><div class=\"row text-center\"><h1 class=\"wow fadeInDown\">{{projectsController.project.title}}</h1><h3 class=\"lead wow fadeInUp\">{{projectsController.project.description}}</h3></div><div id=images class=\"row liltron-text-col\"><div class=\"col-xs-12 col-sm-12 col-md-2 col-lg-2\"></div><div class=\"col-md-8 col-lg-8 project-content\"><h1 class=\"lead text-center\">OVERVIEW</h1><p>Lil\' Tron is a project that two other students and myself built as apart of an Applied Natural Language Processing class. Our goal was to build a program which could generate cool rap lyrics on it\'s own! The app consisted of a front end web app, an artificially intelligent backend.</p><h1 class=lead>WHAT I DID</h1><p>My role in this project was:<ul><li><b>FRONT END:</b> Create a <b>AngularJS</b> front end web app to display auto generated rap lyrics. Grab lyrics from other rap bot websites. Create a voting system to let people choose between our lyrics and competitor\'s lyrics.</li><li><b>BACK END:</b> Create a <b>NodeJS</b> & <b>Python</b> back end to run the Natural Language Processing algorithms my team and I built.</li></ul></p></div><div class=\"col-xs-12 col-sm-12 col-md-2 col-lg-2\"></div></div><div class=\"row text-center\"><h1 class=lead>THE LOOK</h1><div class=\"col-xs-12 col-sm-12 col-md-12 col-lg-12\"><img class=liltron-limit src=assets/images/liltron-iphone.tiff alt=\"\"><h6><b>Figure 1:</b> The Lil Tron web app.</h6><br></div></div><br><div id=roommate class=\"row liltron-text-col\"><br><div class=\"col-md-2 col-lg-2\"></div><div class=\"col-xs-12 col-sm-12 col-md-8 col-lg-8 text-justify\"><p><b>Figure 1</b> The app gives a simple interface. Users could press the \'Rhymify\' button and be presented with two sets of lyrics. Our generated lyrics, and lyrics from another website. They then had the choice of choosing their \'Favorite\' lyric.</p><br><h4 class=text-center><b>Techie Section:</b> What\'s happening behind the scenes?</h4><p><ul><li>AngularJS Web App (Me)</li><li>NodeJS Web Server (Me)</li><li>Python NLP Server (Team)</li><li>MongoDB Persistence (Me)</li></ul></p><h4>NLP Engine</h4><p>Let\'s start from the core. The NLP engine for generating Rap lyrics. To build this engine we needed three things:<ul><li>Language Model (Teammate 1 - Akshay)</li><li>Rhyming \'Goodness\' (Teammate 2 - Gandahli)</li><li>Thesaurus (Me)</li></ul>These three steps were implemented in <b>Python</b>. I\'ll first talk about how we accomplished each task. Then I\'ll talk about how each of the pieces worked together.</p><h5 class=\"lead text-center\">N-GRAM</h5><p>Akshay crawled the lyrics of about 10000 rap songs, and generated an NGram language model. To put it briefly, an ngram language model stores phrases of words, and a probability score (from 0 - 1) for how likely it is to see those phrases. The \'N\' in Ngram says how many words per phrase you store. The NGram allows us to generate words based on previous words seen.</p><h5 class=\"lead text-center\">RHYMING \'GOODNESS\'</h5><p>The next thing that is important, is the notion of rhyming. Generating sentences is cool, but for rap to sound good, we need rhyming words. To do this, Gandhali wrote a program to grab rhyming scores from a website; the website gives numerical scores for how well words rhyme with eachother. So given a word, we have a list of words that rhyme with it, as well as scores for how well each word rhymes.</p><h5 class=\"lead text-center\">RELATED WORDS</h5><p>The last piece is to add some variability to the words the Ngram language model uses. The NGram generates words based on what it has seen already (it\'s vocabulary). The rhyming portion lets you know how well two words rhyme. But now you need add some spontaneity to the rhymes so they don\'t seem stale. This is where a thesaurus comes in. A thesaurus tells you the synonyms for a given word. To add more similar words to the one in our vocabulary, I created a program to grab words from thesaurus websites and store them in a JSON dictionary.</p><h5 class=\"lead text-center\">PUT IT ALL TOGETHER</h5><p>Now that we have all the pieces, let\'s talk about how they fit together.<ol><li>Generate 18 words via the NGram language model. This will be a sentence (aka 2 bars)</li><li>Break the sentence into two pieces</li><li>Look at the last words of piece 1 and piece 2. We\'ll call them \"word-1\" and \"word-2\".</li><li>Use the thesaurus to find the related words/synonyms for \"word-1\" and \"word-2\". We\'ll call those \"related-list-1\" and \"related-list-2\", respectively. Also use part of speech tagging to make sure nouns match with nouns, adjectives with adjectives, etc.</li><li>Iterate through the combinations of words and find the pair that gives you the highest rhyme score</li><li>Put replace \"word-1\" and \"word-2\" with the pair of words you found!</li><li>Repeat this 3 times to get 16 bars!</li></ol></p><br><h5 class=\"lead text-center\">SUMMARY</h5><p>Great so now that we\'ve discussed the NLP engine, let\'s talk about the rest of it; making an application out of this. The goal was to host this online so that peers within our class could try out our application. I was faced with the task of deploying this online.</p><p>I wrote an AngularJS web app to query the backend for rap lyrics. I also wrote the backend server(s) to handle requests. The backend was composed of three pieces:<ul><li>NodeJS webserver</li><li>Python NLP Server</li><li>MongoDB Persistence</li></ul>The NodeJS webserver handled requests from the AngularJS web app. Once the user pressed the \'rhymify\' button on the webapp, the NodeJS server queries the python NLP engine server for lyrics. Once the lyrics are generated, the NodeJS server returns the lyrics back to the web app. All of this was stored on Amazon Web Services. To keep the app running quickly, I stored the language model, thesaurus and rhyme dictionary in memory. This ended up being a little tricky at first because the files were larger then the provided ram (> 4 GB). To get around this I increased the virtual memory (via swap file) and things worked like a charm! It takes about <b>10 seconds</b> from the time \'rhymify\' is clicked to return lyrics to the user! We could definitely have sped this up via multiprocessing or distributing the load, but we also had to implement this within a couple of weeks. Not bad if you ask me!</p><p>The last step happens when a user chooses a \'favorite\' lyric. We store the lyric they chose as well as which site generated the lyric (ours, or the competitor). We give no indication which lyric is ours or the competitors. This was our validation test; it let us see if we were generating good lyrics. After leaving the voting open for a few days, our lyrics <b>outperformed</b> the competitor!</p></div><div class=\"col-md-2 col-lg-2\"></div></div></div>");
$templateCache.put("app/model/projects/mega.html","<div class=\"jumbotron text-center mega-vertical-center mega-jumbotron-bg\"><div class=container><h1 class=\"wow fadeInDown\">{{projectsController.project.title}}</h1><h3 class=\"lead wow fadeInUp\">{{projectsController.project.description}}</h3><h4 class=\"wow fadeInUp\">{{projectsController.project.date}}</h4></div></div><div class=\"container-fluid wow fadeInUp\"><div id=overview class=\"row proxee-text-col\"><div class=\"col-xs-12 col-sm-12 col-md-2 col-lg-2\"></div><div class=\"col-md-8 col-lg-8 project-content\"><h1 class=\"lead text-center\">OVERVIEW</h1><p><a ng-href={{projectsController.project.url}} target=_blank>The Code</a></p><p>When I started getting into web design, I built a website for a student organization called MEGA (Minority Engineering Graduate Association) at USC. At the time, I was the president of the organization, and I was interested in revamping the website.</p><p>I built the mockup website using <b>AngularJS</b>, and <b>Twitter Bootstrap</b>. To make development efficient, I used a command line tools called <code>Grunt</code> and <code>Karma</code>. What do they do?<ul><li><b>Grunt:</b> A task runner that allows you to automate things. Really useful for \'minifying\' and \'uglifying\' your css and js files. Basically that boils down to shrinking your code so that it takes your web browser less time to download and render your website!</li><li><b>Karma:</b> An awesome test harness that allows you to test your JS</li></ul></p></div><div class=\"col-xs-12 col-sm-12 col-md-2 col-lg-2\"></div></div><div class=\"row text-center proxee-text-col\"><h1 class=lead>THE RESULT</h1><div class=\"col-xs-12 col-sm-12 col-md-12 col-lg-12\"><img class=mega-limit src=assets/images/mega-website-wide.tiff alt=\"\"><h6><b>Figure 1:</b> The main page for the MEGA website.</h6><br></div></div><br><div id=roommate class=\"row proxee-text-col\"><br><div class=\"col-md-2 col-lg-2\"></div><div class=\"col-xs-12 col-sm-12 col-md-8 col-lg-8 text-justify\"><p><b>Figure 1</b> shows the final result for the MEGA website. I really wanted to make a simple page that highlighted the most important things about MEGA. The first thing a person would see when they went to the website would be a <code>Carousel</code> aka an <code>Image Slider</code>. The image slider showcased us at social events, community service events and also at professional events like the NSBE national convention. Additionally, I leveraged Twitter Bootstrap to make the website responsive, so that it would look nice on a screen of any size!</p><br></div><div class=\"col-md-2 col-lg-2\"></div></div></div>");
